import { Component } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { Subscription } from 'rxjs';
import { DataStoreService } from '../@core/service/data-store.service';
import { RBACINFO } from '../@core/urls/rbac-url.config';

@Component({
  selector: 'lib-workflow',
  templateUrl: './workflow.component.html',
  styleUrls: ['./workflow.component.scss']
})
export class WorkflowComponent  {
  safeUrl!: SafeResourceUrl;
  environment: any;
  RBACORG: RBACINFO = new RBACINFO();
  orgSubs!: Subscription;
  orgId: any;


  constructor(public sanitizer: DomSanitizer, private _storeservice: DataStoreService) {
     // This is intentional - The code has been reviewed and does not pose a risk. It does not need to be modified.
     this.orgSubs =  this._storeservice.currentStore.subscribe((res: any) => {
      if (res['RBACORG'] && res['RBACORG'] !== '') {
        this.RBACORG = res['RBACORG'];
        this.environment = this.RBACORG['environment'];
        this.orgId = parseInt(this.RBACORG['orgID']);
        if(this.environment){
          this.safeUrl = this.sanitizer.bypassSecurityTrustResourceUrl(this.environment.n8nWorflowUrl);
        }
      }
    });
  }
 ngOnInit(): void {

  }
  ngOnDestroy(): void {
    this.orgSubs.unsubscribe();
  }
}
