import { Component, Input, OnInit } from '@angular/core';

import { Observable } from 'rxjs';
import { PermissionStore } from './pics-workflow/@core/permissions/permission.store';
import { DataStoreService } from './pics-workflow/@core/service/data-store.service';
import { RBACINFO } from './pics-workflow/@core/urls/rbac-url.config';

@Component({
  selector: 'workflow',
  template: `
    <lib-workflow></lib-workflow>
  `,
  styles: [
  ]
})
export class WorkflowComponent implements OnInit {

  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public n8nEvent!: Observable<any>;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService
  ) {
  }
  ngOnInit() {
     this.n8nEvent.subscribe((val: any) => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
    })
  }

}
